
.uploadBox {
    position: relative;
    background: #eee;
    padding: 0 1em 1em 1em;
    margin: 1em;
}
.uploadBox h3 {
    padding-top: 1em;
}
.uploadBox .uploadBoxMain {
    position: relative;
    margin-bottom: 1em;
    margin-right: 1em;
}

/* Drag and drop */
.uploadBox .dropArea {
    position: relative;
    width: 100%;
    height: 3em;
    border: 5px dashed #00ADCE;
    text-align: center;
    font-size: 2em;
    padding-top: 10px;
}
.uploadBox .dropArea input {
    position: absolute;
    cursor: pointer;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
}
/* End drag and drop */

/* Loader */
.uploadBox .loader {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background-color: #fff;
    opacity: 0.9;
}
.uploadBox .loaderImg {
    border: 9px solid #eee;
    border-top: 9px solid #00ADCE;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}
@-webkit-keyframes spin {
0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
}
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
@keyframes spin {
0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
}
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
/* End Loader */
.uploadBox .errorMsg {
    font-size: 2em;
    color: #a94442;
}
.uploadBox .successMsg {
    font-size: 2em;
    color: #3c763d;
}



